import router from '../router';

//  post  { article, articleId }
// article articleId  ID
export const post = ({ commit, state }, { article, articleId }) => {
  // 
  let articles = state.articles;

  // 
  if (!Array.isArray(articles)) articles = [];

  if (article) {
    //  ID  1
    const uid = 1;
    const { title, content } = article;
    const date = new Date();

    //  articleId
    if (articleId === undefined) {
      // 
      const lastArticle = articles[articles.length - 1];

      // 
      if (lastArticle) {
        //  articleId  articleId  1
        articleId = parseInt(lastArticle.articleId) + 1;
      } else {
        //  articleId  1
        articleId = articles.length + 1;
      }

      articles.push({
        uid,
        articleId,
        title,
        content,
        date
      })
    } else { //  articleId, 
      //  articleId 
      for (let article of articles) {
        if (parseInt(article.articleId) === parseInt(articleId)) {
          article.title = title;
          article.content = content;
          break
        }
      }
    }

    commit('UPDATE_ARTICLES', articles);
    //  articleId  showMsg showMsg 
    router.push({ name: 'Content', params: { articleId, showMsg: true }})
  } else {
    for (let article of articles) {
      if (parseInt(article.articleId) === parseInt(articleId)) {
        articles.splice(articles.indexOf(article), 1);
        break
      }
    }

    commit('UPDATE_ARTICLES', articles);
    router.push({ name: 'Home', params: { showMsg: true }});
  }
}
